.about section{
    width: 100%;
    height: 100hv;
    align-items: center;
    margin-bottom: 20px;
    
}
.about_container{
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 80%;
    margin: auto;
  border-radius: 15px;
    height: 70vh;
  
     box-shadow: inset 0px 5px 7px 0px rgba(158, 167, 231, 0.525);
}
.about-tag{
    margin-left: 140px;
    color: white;
}
.about_img{
    display: block;
    width: 40%;
    border-radius: 7%;
}
.about_text{
    width: 50%;
    text-align: center;
}
#user-detail-intro{
      font-size: 16px;
    font-weight: 300;
    text-align: left;
    word-spacing: 2px;

}

.metag{
    margin-left: 140px;
}
   /* <====Media Querry=====> */
  
    @media screen and (max-width: 960px) {
.about-tag{
    color: white;
    margin-left: 5%;
}
.about_container{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 90%;
    height: auto;
    margin: auto;
    border-radius: 10px;
     box-shadow: inset 0px 5px 5px 0px rgba(158, 167, 231, 0.525);
}
.about_img{
    display: block;
    width: 90%;
    margin: auto;
    border-radius: 5px;
    height: 300px
}
.about_text{
    width: 90%;
    text-align: left;
}

    }
     @media screen and (max-width: 460px) {
.about-tag{
    color: white;
    margin-left: 5%;
}
.about_container{
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    width: 90%;
    height: auto;
    margin: auto;
    border-radius: 10px;
     box-shadow: inset 0px 5px 5px 0px rgba(158, 167, 231, 0.525);
}
.about_img{
    display: block;
    width: 90%;
    margin: auto;
    border-radius: 5px;
    height: 35%;
}
.about_text{
    width: 90%;
    text-align: center;
}
#user-detail-intro{
    font-size: 15px;
    word-spacing: 1px;

}

   }
